//https://api.klubo.club/api/documentacion/crear


export const crearDocumentacion = (obj,token) => {
   

    return fetch(`https://api.klubo.club/api/documentacion/crear`,{
            method: "POST",
            headers: {
                Accept: 'application/json',
                "Content-type": "application/json",
                Authorization: `Bearer ${token}`
            },

            body: JSON.stringify(obj)
        })
        .then(response => {
            console.log(response)
            return response.json()
        })
        .catch(err => {
            console.log(err)
        })
    }



    export const getDocumentacionByClub = (club,token) => {


        return fetch(`https://api.klubo.club/api/documentacion/solicitud/${club}`,{
             method: "GET",
             headers: {
                Accept: 'application/json',
                "Content-type": "application/json",
                Authorization: `Bearer ${token}`
            },
         })
         .then(response => {
             return response.json()
         })
         .catch(err => console.log(err))
    
     
       }


