       ///documentacion/eliminar/solicitud/:solicitud


       export const deleteSolicitud = (id,usuario,token) => {
        return fetch(`https://api.klubo.club/api/documentacion/eliminar/solicitud/${id}/${usuario}`,{
                method: "DELETE", 
                    headers: {
                        Accept: 'application/json',
                        "Content-type": "application/json",
                        Authorization: `Bearer ${token}`
                    },
                
            })
            .then(response => {
                console.log(response)
                return response.json()
            })
            .catch(err => {
                console.log(err)
            })
        }
    